/* Custom Scrollbar Styles */

/* For both vertical and horizontal scrollbars */
::-webkit-scrollbar {
    width: 8px; /* Vertical scrollbar width */
    height: 8px; /* Horizontal scrollbar height */
    background-color: #f1f1f1; /* Background color of the scrollbar */
}

/* Horizontal scrollbar - target it to make it thinner */
::-webkit-scrollbar-horizontal {
    height: 6px; /* Thinner horizontal scrollbar */
}

/* Vertical scrollbar thumb */
::-webkit-scrollbar-thumb {
    background-color: #888; /* Color of the scrollbar thumb */
    border-radius: 10px; /* Rounded corners of scrollbar thumb */
}

/* On hover, darken the scrollbar thumb */
::-webkit-scrollbar-thumb:hover {
    background-color: #555; /* Color of scrollbar thumb on hover */
}

/* Firefox custom scrollbar */
.scrollbar-thin {
    scrollbar-width: thin; /* Thin scrollbar */
    scrollbar-color: #888 #f1f1f1; /* Thumb and track colors */
}

